.App {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
}

.App__video {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
}

video::-webkit-media-controls {
  display: none;
}

/* Скрыть полосу прокрутки на мобильных устройствах (не обязательно) */
video::-webkit-media-controls-enclosure {
  display: none;
}